h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

body{
	font-family: $mainFont;
	color: $colorGray;
	background: #F4F4F4;
	-webkit-font-smoothing: antialiased;
	&:not(.active){
		overflow: hidden !important;
	}
}

*::-webkit-scrollbar {
	display: none;
}

* {
	-ms-overflow-style: none;
	scrollbar-width: none;
	box-sizing: border-box;
}


img{
	display: block;
}

.wrapper{
	padding-left: vw(91);
	@include max(md){
		padding-left: 0;
	}
}


.blockTitle{
	@include title;
}

.horisontalBlock{
	@include block;
}

.horisontalBlock__flex{
	@include blockFlex;
}

.allHeightImage{
	@include allHeightImage;
}

.paddingsImage{
	@include paddingsImage;
}

.yearConner{
	@include year;
}
.blockOverlay{
	@include overlay;
}
.textBlock{
	@include text;
}